@import "../../constant.scss";

.side-menu-toggle-btn-group {
  display: flex;

  .side-menu-show-btn {
    cursor: pointer;
    font-size: 1.5rem !important;
    font-weight: bold;
    display: flex;
    align-items: center;

    span {
      margin-left: 0.5rem;
    }
  }
}

.side-menu {
  transition: all 0.2s ease-out;
  width: 350px;
  height: calc(100vh - 4rem);
  position: absolute;
  top: 4rem;
  right: -360px;
  background-color: color(light);
  color: color(primary-dark);
  z-index: 10;
  box-shadow: 0 7.3px 3.6px rgba(0, 0, 0, 0.28),
    0 20.2px 10px rgba(0, 0, 0, 0.182), 0 48.5px 24.1px rgba(0, 0, 0, 0.14),
    0 161px 80px rgba(0, 0, 0, 0.098);
  display: flex;
  flex-direction: column;

  &.show-menu {
    right: 0;
  }

  .side-menu-group {
    border-bottom: 1px solid color(gray-trans);
  }
}

.side-menu-header {
  padding: 1rem 1rem 0 1rem;
  .side-menu-group {
    display: grid;
    grid-template-columns: 4rem 1fr 4rem;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding-bottom: 0.5rem;

    .center-group {
      font-size: 1.8rem;
      font-weight: bold;
      margin: 0;
    }
  }

  .side-menu-close-btn {
    font-size: 2.5rem;
    cursor: pointer;
  }
}

.side-menu-body {
  padding: 1rem 1rem 3rem 1rem;
  flex-grow: 1;
  overflow-y: auto;
}

.background-color-swatch {
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-by-label {
  padding: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .search-by-label-reset-btn {
    padding: 12px;
    font-size: 15px;
    border-radius: 50%;
    background-color: color(gray-trans);
    opacity: 0.6;

    &.active {
      cursor: pointer;
      opacity: 0.9;

      &:hover {
        opacity: 1;
      }
    }
  }
}

hr {
  border: 1px solid color(gray-trans);
  border-bottom: none;
  margin: 1rem 0;
}
